.portofolio-header {
  margin-left: 45px;
  margin-top: 300px; }

.portofolio-main {
  margin-left: 5px;
  margin-top: 0;
  font-size: 2rem; }

.portofolio-p {
  margin-left: 10px; }

header {
  height: 100px; }
  header .inner-header {
    position: relative;
    z-index: 10;
    height: 100px;
    display: flex;
    justify-content: space-between;
    align-items: center; }
    header .inner-header .logo a {
      font-weight: 700;
      font-size: 2rem;
      text-decoration: none;
      color: teal; }
    header .inner-header .logo a:hover {
      color: #19f3f3; }
    header .inner-header .menu button {
      border: none;
      background: none;
      outline: none;
      cursor: pointer;
      font-size: 1.5rem;
      mix-blend-mode: difference;
      color: teal; }
    header .inner-header .menu button:hover {
      color: #19f3f3; }
  header .hamburger-menu {
    display: none;
    z-index: 9;
    top: 0;
    left: 0;
    right: 0;
    position: fixed;
    height: 100%;
    width: 100%; }
    header .hamburger-menu .menu-secondary-background-color {
      background: #474444;
      top: 0;
      left: 0;
      right: 0;
      bottom: 0;
      position: fixed;
      height: 100%;
      width: 100%;
      z-index: -1; }
    header .hamburger-menu .menu-layer {
      position: relative;
      background: #2b2b2b;
      height: 100%;
      overflow: hidden; }
      header .hamburger-menu .menu-layer .menu-city-background {
        top: 0;
        left: 0;
        right: 0;
        bottom: 0;
        position: absolute;
        height: 100%;
        width: 100%;
        opacity: 0;
        background-size: cover;
        background-repeat: no-repeat;
        -webkit-animation-name: backgroundEffect;
                animation-name: backgroundEffect;
        -webkit-animation-duration: 30s;
                animation-duration: 30s;
        -webkit-animation-iteration-count: infinite;
                animation-iteration-count: infinite; }
      header .hamburger-menu .menu-layer .wrapper {
        position: relative; }
        header .hamburger-menu .menu-layer .wrapper .menu-links {
          display: flex;
          justify-content: space-between;
          align-items: center;
          position: relative;
          top: 200px; }
          header .hamburger-menu .menu-layer .wrapper .menu-links nav {
            display: block; }
            header .hamburger-menu .menu-layer .wrapper .menu-links nav ul {
              margin: 0;
              padding: 0; }
              header .hamburger-menu .menu-layer .wrapper .menu-links nav ul li {
                list-style: none;
                font-size: 6rem;
                font-weight: 700;
                cursor: pointer;
                height: 135px;
                overflow: hidden;
                position: relative;
                width: 700px; }
                header .hamburger-menu .menu-layer .wrapper .menu-links nav ul li a {
                  position: absolute;
                  color: #474444;
                  text-decoration: none; }
                  header .hamburger-menu .menu-layer .wrapper .menu-links nav ul li a:hover {
                    color: #fff; }
          header .hamburger-menu .menu-layer .wrapper .menu-links .info {
            color: #fff;
            width: 300px; }
            header .hamburger-menu .menu-layer .wrapper .menu-links .info h3 {
              font-size: 1.2rem;
              margin: 8px auto; }
            header .hamburger-menu .menu-layer .wrapper .menu-links .info p {
              margin: 0 auto;
              font-size: 0.8rem; }
        header .hamburger-menu .menu-layer .wrapper .locations {
          position: absolute;
          bottom: -80px;
          color: #fff;
          margin-top: 16px;
          font-size: 0.8rem;
          font-weight: 600; }
          header .hamburger-menu .menu-layer .wrapper .locations span {
            cursor: pointer;
            font-weight: 400;
            margin: 0 32px;
            -webkit-transition: 0.3s ease-in-out;
            transition: 0.3s ease-in-out; }
            header .hamburger-menu .menu-layer .wrapper .locations span:first-child {
              margin-left: 64px; }
            header .hamburger-menu .menu-layer .wrapper .locations span:hover {
              color: #fff;
              background: #474444;
              padding: 8px 24px;
              border-radius: 4px; }

.profile {
  width: 300px;
  height: inherit; }

.icon {
  font-size: 30px;
  margin: 10px; }

@-webkit-keyframes backgroundEffect {
  0% {
    background-position: 0% 0%; }
  25% {
    background-position: 40% 10%; }
  50% {
    background-position: 0 10%; }
  75% {
    background-position: 10% 40%; }
  100% {
    background-position: 0% 0%; } }

@keyframes backgroundEffect {
  0% {
    background-position: 0% 0%; }
  25% {
    background-position: 40% 10%; }
  50% {
    background-position: 0 10%; }
  75% {
    background-position: 10% 40%; }
  100% {
    background-position: 0% 0%; } }

